body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    background-color: #dddddd;
    color: #252525;
}

.author-section {
    padding: 25px 0;
}

.flag {
  transition: all 0.2s linear;
}

  .flag[disabled] {
      opacity: 0.5;
      -webkit-transform: scale(0.9);
              transform: scale(0.9);
  }

  .flag:hover {
    background: rgba(128,128,128,0.3);
  }

.card {
    padding: 15px;
    border-radius: 0;
    border-style: none;
}

.collaborator-container {
    display: flex;
    flex-direction: column;
    text-align: center;
    color: white;
    font-size: 1.1em;
    margin: 2em 0;
}

.collaborator {
    width: 60px;
    height: 60px;
    border-radius: 50%;
}

.team {
    margin-top: 50px;
}

.description {
    padding-bottom: 15px;
    /* margin-bottom: 50px; */
    border-bottom: dashed 2px #252525;
}

.btn-primary {
    width: 100%;
}

.text-wrap {
    text-align: right;
}
.header-inner small {
  font-size: 60%;
}

.author-search {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
}

.list {
  padding-top: 20px;
}

.timeline h4 {
  font-size: 1.1em;
}
.Gallery {
    max-width: 500px;
    width: 100%;
    margin:  0 auto;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.bigpicture.link {
  position: relative;
}

  .bigpicture.link:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255,255,255,0.5) url(/CodeJam-5/static/media/play.7944df03.svg) no-repeat center center / auto 0%;
    transition: all 0.3s linear;
    opacity: 0;
  }

  .bigpicture.link:hover:before {
    opacity: 1;
    cursor: pointer;
    background-size: auto 50%;
  }
.author__page__header {
  border-bottom: 2px solid #ccc;
  text-align: center;
}

.video_container {
  display: flex;
  flex-direction: column;
}

.preview {
  width: 500px;
  height: 300px;
}
